<template lang="jade">

  .component 

    .fat-only 
      .event-list 
        .item(v-for = "(ev,idx) in timeline", :class="['dark','gloom','light'][idx % 3]") 
          .big {{ev.start}}  {{ev.end}} 
          .small 
            p {{ ev.title }} 
            .title {{ ev.info }} 
          .small 
            p  
            Plink(:urllink="ev.link")

    .thin-only
      .event-list 
        .item(v-for = "(ev,idx) in timeline", :class="['dark','gloom','light'][idx % 3]") 
          .big {{ev.start}}  {{ev.end}} 
          .small 
            p {{ ev.title }} 
            .title {{ ev.info }} 
          .small 
            p  
            Plink(:urllink="ev.link")

</template>

<script>

  import axios from 'axios'
  import Plink from './ParticipationLink.vue'

  export default {

    props: ['article'],
    components: {
      Plink
    },
    data() {
      return {
        timeline: [] // 
      }
    },
    methods: {
      getTimeline(id) {
        axios.get('https://talk.vtaiwan.tw/t/' + id + '.json?include_raw=1')
          .then((response) => {
            var detail_info = response.data;
            detail_info = detail_info['post_stream']['posts'].slice(1); // 

            for (var i in detail_info) {
              var regex = /(?: (?:init )?)|\n/g; // 
              var date_regex = /^\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])$/g; //yyyy-mm-dd(ex:2016-10-31)
              var time_regex = /^(2[0-3]|1[0-9]|0[0-9]|[^0-9][0-9]):([0-5][0-9]|[0-9]):([0-5][0-9]|[0-9])$/g; // hh:mm:ss(ex:19:00:00)
              var timeline_content = {}; // 
              var comment = {}; // 
              var links = []; // 
              timeline_content['title'] = detail_info[i]['raw'].split(regex)[0]; // 
              timeline_content['start'] = detail_info[i]['raw'].split(regex)[1]; // 

              comment = detail_info[i]['raw'].split(regex); // ()

              if (timeline_content['start'].length > detail_info[i]['raw'].split(regex)[2].length) { // ""  
                timeline_content['start'] = timeline_content['start'] + " " + detail_info[i]['raw'].split(regex)[2];
                timeline_content['end'] = null;
              }
              else { // 
                timeline_content['end'] = " " + detail_info[i]['raw'].split(regex)[2]; // 
              }
              if (detail_info[i]['raw'].split(regex)[2].length > 10) { // 
                timeline_content['end'] = null;
              }
              for (var j = 1; j < comment.length; j++) { // 
                if (comment[j].indexOf("http") > -1) { // "http" -> 
                  links.push(detail_info[i]['raw'].split(regex)[j]);
                }
                if (comment[j].indexOf("http") == -1 && comment[j].match(date_regex) == null && comment[j].match(time_regex) == null) { //  ->  
                  timeline_content['info'] = comment[j];
                }
              }
              timeline_content['link'] = links;
              this.timeline.push(timeline_content);
              
            }
            return this.timeline;
          })
      }
    },
    created: function () {
      this.getTimeline(this.article.id);
    },
    // updated: function () {
    //   this.getTimeline(this.article.id);
    // }

  }


</script>

<style lang="scss" scoped>

.event-list {
  display: flex;
  flex-flow: column nowrap;
  .item {
    display: flex;
    text-align: left;
    padding: 1em;
    font-size: 1.2rem;
    &.dark { background-color: #eeeeee }
    &.gloom { background-color: #f4f4f4 }
    &.light { background-color: #fbfbfb }
    .big {
      display: flex;
      align-items: center;
    }
    .small {
      flex: 2 0 6em;
      p {
        font-weight:600;
        margin-bottom: 1.2rem;
      } 
    }
  }
}
.fat-only {
  .big {
        flex: 1 0 15em;
  }
}
.thin-only {
  .item {
    padding: 1em 0 1em 0.5em;
     font-size:1.1rem;
    .big {
          flex: 1 0 6em;
    }
  }  
}



</style>


