<template lang="jade">
  .component

    .fat-only
      .step-progress-bar
        ul.progress-bar
          li(v-for="(s,idx) in steps", v-bind:class='{active:s.active}') {{s.title}}

    .thin-only
      .step-progress-bar
        ul.progress-bar
          li(v-for="(s,idx) in steps", v-bind:class='{active:s.active}') {{s.title}}
     
</template>

<script>
import axios from 'axios'

export default {

    props:['article'],

    data(){
        return{
            steps:[],
        }
    },
    methods:{
      getProgress(id){
        axios.get('https://talk.vtaiwan.tw/t/'+ id +'.json?include_raw=1')
        .then((response=>{
            var detail_info = response.data;
            var steps = [
              {
                title:"",
                active:false
              },
              {
                title:"",
                active:false
              },
              {
                title:"",
                active:false
              },
              {
                title:"",
                active:false
              },
              {
                title:"",
                active:false
              }
            ];
            detail_info = detail_info['post_stream']['posts'].slice(1); // 

            var end = detail_info.length-1;

            var current = detail_info[end]['raw'].split(" ")[0]; //
            this.status = current;

            for(var i in detail_info){ //
                var init  = detail_info[end]['raw'].split(" ")[1]; // if init

                for (var j in steps){
                    steps[j]['active'] = true;
                    if(steps[j]['title'] === current && init === 'init'){ //" init",activevisited
                        return this.steps = steps; // array
                    }
                    if(steps[j]['title'] === current){ //"",activevisited
                        this.steps = steps;
                        return this.steps = steps;// array
                    }
                }
            }
        }))
      }
    },
    created:function(){
        this.getProgress(this.article.id);
    },
    updated:function(){
        this.getProgress(this.article.id);
    }


}
    
</script>

<style lang="scss" scoped>

@import "../sass/global.scss";

.step-progress-bar {
  width: 600px;
  margin: 0 auto;
  display: block;
  height: 100px; 
  padding: 10px;
}
ul.progress-bar {
  counter-reset: step;
  padding:0;
}
.step-progress-bar li {
  list-style-type: none;
  float: left;
  color: #40B3BF;
  width: 20%;
  position: relative;
  text-align: center;
  font-weight: 600;
  font-size:1.2rem;
}
.step-progress-bar li:before {
  content: counter(step);
  counter-increment: step;
  width: 30px;
  height: 30px;
  border: 2px solid #40B3BF;
  border-radius: 50%;
  display: block;
  text-align: center;
  line-height: 30px;
  margin: 0 auto 10px auto;
  z-index: 9;
  background-color: white;
  font-weight: 600;
}
.step-progress-bar li:after {
  content: "";
  width: 100%;
  position: absolute;
  height: 2px;
  background-color: #40B3BF;
  top: 16px;
  left: 50%;
  z-index: -1;
}
.step-progress-bar li:last-child:after {
  content: none;
}
.step-progress-bar li.active {
  color: #DB5252;

}
.step-progress-bar li.active:before{
  border-color: #DB5252;
}
.step-progress-bar li.active:after{
  background-color: #DB5252;
}

.thin-only { 
  .step-progress-bar {
    width:100%;
    li {
      font-size:1rem;
    }
  }
}

</style>


